,CACA,cAAA,CAEA,0DACE,UA6XgC,CA1XlC,4DACE,+BAhBoE,CAmBtE,6DACE,QAAA,CAKF,qEACE,4FAAA,CAuXA,8CA3ZF,YAAA,CACA,kBA2Z4B,CA1Z5B,qBA0ZiC,CAzZjC,iBAyZwC,CACpC,OAAA,CACA,UAAA,CAEA,qDACE,eAAA,CACA,gBAAA,CACA,aAAA,CAGF,oDACE,WAAA,CACA,WAAA,CAGF,uDAlYJ,UAmYwB,CAlYxB,WAkYwB,CAraxB,gCAAA,CAsCA,qBAAA,CACA,eAAA,CACA,YAAA,CACA,cAAA,CAQE,iBAAA,CAqXI,cAAA,CA3XN,+DACE,eAAA,CACA,oBAAA,CAMA,+DACE,gCAAA,CAqXA,+GAEE,MAAA,CAEA,eAAA,CACA,WAAA,CACA,YAAA,CACA,8BAAA,CAEA,qCAAA,CAEA,oCAAA,CAEA,2HAtbN,+BAAA,CA0bM,2HACE,wCAAA,CAIJ,oDACE,gBAAA,CACA,kBAAA,CACA,eAAA,CAGF,2DACE,cAAA,CACA,WAAA,CAGF,yDACE,wBAAA,CAGF,+GA9cJ,4CAAA,CAidM,eAAA,CACA,YAAA,CACA,WAAA,CACA,iBAAA,CAEA,qCAAA,CACA,gBAAA,CAEA,cAAA,CACA,gBAAA,CAIA,sEAreN,YAAA,CACA,kBAqegC,CApehC,qBAoeqC,CAnerC,kBAJ+D,CAwevD,OAAA,CACA,UAAA,CAEA,wFACE,YAAA,CAGF,sGACE,SAAA,CAxeV,4CAAA,CA4eQ,4EA5eR,4CAAA,CA8eU,gBAAA,CACA,iBAAA,CAEA,UAAA,CACA,eAAA,CAEA,cAAA,CACA,gBAAA,CAEA,qFAAA,kCAAA,CACA,qFAAA,kCAAA,CACA,qFAAA,oCAAA,CACA,qFAAA,mCAAA,CACA,qFAAA,mCAAA,CAMR,mDAxgBF,YAAA,CACA,qBAwgB4B,CAvgB5B,qBAugBoC,CAtgBpC,iBAsgB2C,CACvC,OAAA,CACA,YAAA,CACA,UAAA,CAEA,iBAAA,CACA,eAAA,CACA,yBAAA,CAEA,4DAjdJ,OAAA,CAjEA,YAAA,CACA,kBAkEwB,CAjExB,qBAiE6B,CAhE7B,kBAJ+D,CAqE/D,OAAA,CACA,UAAA,CACA,eAAA,CAEA,iBAAA,CACA,WAAA,CAEA,cAAA,CACA,gBAAA,CACA,oCAAA,CAEA,gEACE,WAAA,CAGF,8DACE,eAAA,CACA,gBAAA,CACA,eAAA,CAGF,kEACE,wCA1B0B,CAsdxB,mEAthBJ,YAAA,CACA,kBAshB8B,CArhB9B,qBAqhBmC,CAphBnC,kBAJ+D,CAyhBzD,eAAA,CACA,UAAA,CACA,OAAA,CAEA,6EAnfN,UAof0B,CAnf1B,WAmf0B,CAthB1B,gCAAA,CAsCA,qBAAA,CACA,eAAA,CACA,YAAA,CACA,cAAA,CAEA,qFACE,eAAA,CACA,oBAAA,CA4eI,0EACE,eAAA,CACA,uBAAA,CACA,cAAA,CACA,YAAA,CAEA,kBAAA,CACA,eAAA,CAEA,oCAAA,CAEA,gFApiBR,+BAAA,CAwiBQ,6EACE,YAAA,CAGF,4EACE,cAAA,CACA,kBAAA,CAIJ,+EAzjBN,YAAA,CACA,kBAFgC,CAGhC,sBAH+C,CAI/C,kBAJ+D,CAa/D,UA8iBwB,CA7iBxB,WA6iB8B,CA3iB9B,iBAAA,CACA,8BAAA,CACA,OAAA,CACA,mDAAA,CACA,cAAA,CAyiBQ,eAAA,CACA,iBAAA,CACA,YAAA,CAziBR,mFACE,UAoiBkC,CAjiBpC,qFACE,wCAgiBuC,CA7hBzC,sFACE,QAAA,CAKF,sFACE,4FAAA,CA6hBI,qGACE,YAAA,CAKN,yDAvkBF,YAAA,CACA,qBAukB4B,CAtkB5B,qBAskBoC,CArkBpC,iBAqkB2C,CACvC,QAAA,CAEA,wBAAA,CACA,YAAA,CAGF,0DACE,YAAA,CACA,qBAAA,CAEA,gEAnlBJ,YAAA,CACA,qBAmlB8B,CAllB9B,qBAklBsC,CAjlBtC,iBAilB6C,CACvC,OAAA,CACA,yBAAA,CAEA,iFACE,+CAAA","sourcesContent":["$clr-primary: #1e1e58;\n$clr-secondary: #a8a8d4;\n\n\n$clr-alert: (\n  \"clr-alert-1\": #0a2e46cc,\n  \"clr-alert-2\": #0a610acc,\n  \"clr-alert-3\": #8e9627cc,\n  \"clr-alert-4\": #835417cc,\n  \"clr-alert-5\": #971010cc,\n);\n\n:export {\n  @each $key, $value in $clr-alert {\n    #{unquote($key)}: $value;\n  }\n}\n\n@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap');\n\n@mixin flex-container($direction: row, $justify: center, $align: center) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify;\n  align-items: $align;\n}\n\n@mixin box-shadow($color: #000000aa, $top: 0, $left: 0, $blur: 10px, $spread: 0px) {\n  box-shadow: $top $left $blur $spread $color;\n}\n\n@mixin button($width: 24px, $height: 24px, $image-size: 80%, $hover-bg: #00000033) {\n  @include flex-container;\n  width: $width;\n  height: $height;\n\n  border-radius: 8px;\n  background-color: transparent;\n  scale: 1;\n  transition: background-color 0.3s ease, scale 0.3s ease;\n  cursor: pointer;\n\n  img {\n    height: $image-size;\n  }\n\n  &:hover {\n    background-color: $hover-bg;\n  }\n\n  &:active {\n    scale: 0.9;\n  }\n}\n\n@mixin delete-button() {\n  :hover {\n    filter: invert(9%) sepia(94%) saturate(5163%) hue-rotate(2deg) brightness(92%) contrast(113%);\n  }\n}\n\n@mixin checkbox($size: 20px, $circle: true) {\n  width: $size;\n  height: $size;\n\n  @include box-shadow(#000000, 0px, 0px, 2px, 0.3px);\n  background-color: white;\n  appearance: none;\n  outline: none;\n  cursor: pointer;\n\n  &:checked {\n    appearance: auto;\n    accent-color: lighten($clr-primary, 20%);\n  }\n\n  @if $circle {\n    border-radius: 50%;\n\n    &:checked {\n      clip-path: circle(50% at 50% 50%);\n    }\n  }\n}\n\n@mixin new-button($hover-clr: #00000011) {\n  order: 1;\n\n  @include flex-container(row, start);\n  gap: 4px;\n  width: 100%;\n  padding: 6px 8px;\n  \n  border-radius: 8px;\n  opacity: 50%;\n\n  cursor: pointer;\n  user-select: none;\n  transition: background-color 0.2s ease;\n\n  img {\n    height: 20px;\n  }\n\n  p {\n    font-size: 0.9rem;\n    font-weight: bold;\n    padding-top: 2px;\n  }\n\n  &:hover {\n    background-color: $hover-clr;\n  }\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nimg {\n  -webkit-user-drag: none;\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n}\n\nbody {\n  display: grid;\n  grid-template: 100px 1fr / 300px 1fr;\n\n  font-family: \"Josefin Sans\", sans-serif;\n  min-height: 100vh;\n\n  overflow: hidden;\n\n\n  ::-webkit-scrollbar {\n    width: 8px;\n  }\n\n  /* Track */\n  ::-webkit-scrollbar-track {\n    box-shadow: inset 0 0 5px #777;\n    border-radius: 10px;\n  }\n\n  /* Handle */\n  ::-webkit-scrollbar-thumb {\n    background: rgba($clr-primary, 0.6);\n    border-radius: 16px;\n  }\n}\n\n.header {\n  @include box-shadow(#00000077);\n  @include flex-container;\n  grid-column: span 2;\n\n  background-color: $clr-primary;\n  padding: 24px 36px;\n\n  position: relative;\n  z-index: 1;\n\n  user-select: none;\n\n  .sidebar-btn {\n    @include flex-container;\n    @include button(50px, 50px, 70%);\n    position: absolute;\n    left: 18px;\n  }\n\n  .logo {\n    @include flex-container;\n    gap: 12px;\n\n    &__icon {\n      height: 64px;\n    }\n\n    &__text {\n      color: white;\n      font-size: 2.1rem;\n      letter-spacing: 1px;\n    }\n  }\n}\n\n.sidebar {\n  @include box-shadow(#00000055);\n  @include flex-container(column, start, start);\n\n  background-color: $clr-secondary;\n  padding: 24px 36px;\n\n  user-select: none;\n\n  &--hidden {\n    display: none;\n  }\n\n  &--hidden + .main {\n    grid-column-start: 1;\n  }\n\n  &__section-header {\n    color: $clr-primary;\n\n    width: 100%;\n    margin-bottom: 8px;\n    border-bottom: 2px solid $clr-primary;\n  }\n\n  &__section {\n    @include flex-container(column, start, start);\n    gap: 4px;\n    padding-left: 8px;\n    margin-bottom: 32px;\n    width: 100%;\n\n\n    .filter {\n      @include flex-container(row, start);\n      gap: 8px;\n      flex: none;\n\n      height: 40px;\n      width: 100%;\n      padding: 6px 8px;\n      border-radius: 8px;\n\n      background-color: transparent;\n      transition: background-color 0.1s ease;\n      cursor: pointer;\n\n      &__icon {\n        height: 28px;\n      }\n\n      &__title {\n        font-size: 1.25rem;\n        padding-top: 4px;\n\n        transform: translateY(2px);\n      }\n\n      &--active {\n        background-color: #8686d8;\n        border-radius: 0px 8px 8px 0px;\n        border-left: $clr-primary 3px solid;\n      }\n\n      &:hover:not(&--active) {\n        background-color: darken($clr-secondary, 5%);\n      }\n    }\n\n\n    &--projects {\n      max-height: 180px;\n      overflow-x: hidden;\n      overflow-y: auto;\n\n      &__new-btn {\n        @include new-button(darken($clr-secondary, 5%));\n        height: 40px;\n        opacity: 60%;\n      }\n\n      .filter {\n        padding-right: 16px;\n\n        &__title {\n          flex: 1 1 auto;\n        }\n\n        &__btn {\n          @include button(28px, 28px, 70%, #00000022);\n          display: none;\n          opacity: 90%;\n\n          &--delete {\n            @include delete-button();\n          }\n        }\n\n        &:hover .filter__btn {\n          @include flex-container;\n        }\n      }\n    }\n  }\n}\n\n.main {\n  padding: 36px;\n  grid-column-end: -1;\n  background-color: #fefefe;\n\n  &__header {\n    margin-bottom: 18px;\n    color: $clr-primary;\n    font-size: 2.2rem;\n  }\n\n  .task-list {\n    @include flex-container(column, start, center);\n    gap: 12px;\n    height: 380px;\n    width: 100%;\n    overflow-x: hidden;\n    overflow-y: auto;\n\n    &__new-btn {\n      @include new-button();\n    }\n\n    .task {\n      @include flex-container(row, start);\n      flex: none;\n      gap: 12px;\n      width: 100%;\n      height: 40px;\n      padding: 8px 12px;\n\n      @include box-shadow(#00000033, 0, 0, 5px);\n      background-color: lighten($clr-secondary, 24%);\n      border-radius: 2px 8px 8px 2px;\n      border-left: 4px solid map-get($map: $clr-alert, $key: \"clr-alert-1\");\n\n      &__is-done {\n        @include checkbox();\n      }\n\n      &__is-done:checked ~ .task__title {\n        color: #777;\n        text-decoration: line-through;\n      }\n      \n      &__is-done:checked ~ .task__date-display {\n        color: #777;\n      }\n\n      &__title {\n        font-size: 1.2rem;\n        padding-top: 4px;\n      }\n\n      &__expand-btn {\n        @include button(24px, 24px, 60%, #eeeeee);\n        display: none;\n        margin-left: 2px;\n\n        @include box-shadow(#00000033, 0, 0, 4px);\n        background-color: #ffffff;\n        opacity: 80%;\n      }\n\n      &__date-display {\n        margin-left: auto;\n\n        font-size: 1.1rem;\n        font-weight: normal;\n      }\n\n      &:hover {\n        background-color: lighten($clr-secondary, 15%);\n    \n        .task__expand-btn {\n          @include flex-container;\n        }\n      }\n    }\n  }\n}\n\n.task-interface {\n  position: absolute;\n  z-index: 2;\n  top: 50%;\n  left: 50%;\n  transform: translateX(-50%) translateY(-50%);\n\n  width: 700px;\n  height: 500px;\n\n  @include box-shadow;\n  border-radius: 16px;\n  border: none;\n  outline: none;\n\n  &::backdrop {\n    background-color: black;\n    opacity: 40%;\n  }\n\n  .modal-children-wrapper {\n    position: absolute;\n    z-index: 2;\n    width: 100%;\n    height: 100%;\n    \n    display: grid;\n    grid-template: 40px 1fr / 2fr 1fr;\n\n    overflow: hidden;\n\n    & > * {\n      padding: 8px;\n  \n      @include box-shadow(#000000aa, 0px, 0px, 2px, 0px);\n    }\n\n    .top-bar {\n      grid-column: span 2;\n\n      @include flex-container(row, space-between);\n      background-color: lighten($clr-secondary, 15%);\n  \n      &__btn {\n        @include button(32px, 32px, 70%);\n  \n        &--delete {\n          @include delete-button();\n        }\n      }\n    }\n\n    .pair {\n      @include flex-container(row, start, start);\n      gap: 8px;\n      width: 100%;\n\n      &__label {\n        padding-top: 6px;\n        font-size: 1.2rem;\n        color: darken($clr-primary, 10%);\n      }\n\n      &__icon {\n        height: 24px;\n        opacity: 40%;\n      }\n\n      #is-done {\n        @include checkbox(24px);\n        margin-top: 4px;\n      }\n\n      #name,\n      #description {\n        flex: 1;\n\n        padding: 6px 8px;\n        border: none;\n        outline: none;\n        background-color: transparent;\n\n        font-family: \"Josefin Sans\", sans-serif;\n\n        transition: background-color 0.2s ease;\n\n        &:focus {\n          @include box-shadow(black, 0px, 0px, 1px, 0px)\n        }\n\n        &:hover {\n          background-color: #00000005;\n        }\n      }\n\n      #name {\n        font-size: 1.5rem;\n        white-space: nowrap;\n        overflow: hidden;\n      }\n\n      #description {\n        font-size: 1rem;\n        resize: none;\n      }\n\n      &--checklist {\n        margin: 12px 0px -4px 0px;\n      }\n\n      #project,\n      #due-date {\n        @include box-shadow(#000000dd, 0, 0, 2px, 0);\n        padding: 6px 8px;\n        outline: none;\n        border: none;\n        border-radius: 6px;\n\n        font-family: \"Josefin Sans\", sans-serif;\n        font-size: 0.85rem;\n\n        cursor: pointer;\n        user-select: none;\n      }\n\n      &--priority {\n        .pair-wrapper {\n          @include flex-container(row, start);\n          gap: 8px;\n          width: 100%;\n\n          input[type=\"radio\"] {\n            display: none;\n          